@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
  font-style: normal;
}

body {
  margin-top: 20px;
}

.form {
  flex-direction: column;
  gap: 20px;
  display: flex;
  position: relative;
}

.form__bar {
  box-sizing: border-box;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  font-family: Avenir, Helvetica, sans-serif;
  font-weight: 400;
}

.form__bar--big {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__bar--small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__bar--big::placeholder, .form__bar--small::placeholder {
  color: #3d4e61;
  font-weight: 400;
}

.form__bar--big:hover, .form__bar--small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.form__bar--big:focus {
  box-shadow: (0 4px 4px #3d4e6133);
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
}

.form__bar--small:focus {
  box-shadow: none;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
}

.form__label-big {
  z-index: 1;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 25px;
  left: 26px;
}

